.App{
    background: rgb(247, 250, 87);
    padding: 20px;
}

.Form{
    display: flex;
}
.btnForm{
    background: rgb(84, 216, 84);
    border: none;
    
    cursor: pointer;
}

.h3Comunity{
    text-align: center;
}
.UlListBlog{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 25px;
    padding: 10px;
    background-color: rgb(240, 168, 85);
    flex-wrap: wrap;
    list-style: none;
    border-radius: 10px;
}

.LiListBlog{
    position: relative;
    text-align: center;
    width: 25%;
    border: 2px rgb(134, 90, 39) solid;
    border-radius: 10px;
}

.btnDelete{
    position: absolute;
    cursor: pointer;
    background: none;
    border: none;
    right: 0;
    top: 0;
}

.btnLike{
    position: absolute;
    right: 0;
    bottom: 0;
    cursor: pointer;
    background: none;
    border: none;
    margin-bottom: 5px;
    font-size: 20px;
}